.App-link {
  color: #61dafb;
}

::-webkit-scrollbar{
  width: 5px;
}

::-webkit-scrollbar-thumb{
  background-image: linear-gradient(rgb(68, 68, 68), rgb(255, 255, 255));
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover{
  background-image: linear-gradient( rgb(255, 255, 255), rgb(68, 68, 68));
}

.form-line-active input:focus, .form-line-active textarea:focus {
  border: 1px solid green !important;
  box-shadow: 0 0 3px green !important;
  -moz-box-shadow: 0 0 3px green !important;
  -webkit-box-shadow: 0 0 3px green !important;
  }
  
  input:focus, textarea:focus, select:focus {
  outline-offset: 0px !important;
  outline: none !important;
  }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
